html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	background: radial-gradient(ellipse at bottom, #1e2f42 0%, #090a0f 100%);
	overflow: scroll;
	position: absolute;
	z-index: -2;
	// background-image: url(../assets/background.png);
}

body {
	line-height: 1;
}

ol,
ul,
li {
	list-style: none;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

section {
	justify-content: center;
}

h1,
h2,
h3,
h4,
h5,
p,
li,
a {
	font-family: $font-sans-serif
}

a {
	cursor: pointer;
}

h1 {
	font-size: 40px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 16px;
}

h4 {
	font-size: 12px;
}

ul {
	list-style: none;
}

p {
	font-size: 14px;
	font-weight: 300;
}

.flex {
	display: flex;
}

.flex-column {
	display: flex;
	flex-direction: column;
}